@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,400;0,500;0,600;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', 'Montserrat', 'Source Sans Pro' sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow: scroll;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

 .navbar {
    position: -webkit-sticky;
    position: sticky;
    display: flex;
    height: 95px;
    padding: 1%;
    flex-direction: row;
    max-width: 100vw;
    background-color: #004165;
    box-shadow: 0px 1px 5px #888, 0px 0px 5px #a9b2b1;
    color: #ffffff;
    vertical-align: baseline;
    z-index: 3;
    right: 0;
    left: 0;
    top: 0;
}


.header-info {
    width: auto;
    display: flex;
    text-align: center;
    flex-direction: row;
    justify-content: center;
    font-family: Source Sans Pro;
    font-weight: 600;
    font-size: 1.25rem;
}
.button {
    border-radius: 25px;
    background: #4bb543;
    padding: 20px;
    width: 180px;
    height: 25px;
    margin: 1;
    float: inline-end;
}

.button li {
    text-decoration: none;
    color: white;
    font-family: Source Sans Pro;
    font-weight: 600;
    font-size: 1.25rem;
}

.navbar-menu {
    /* margin-top: px; */
    top: 50%;
    position: absolute;
    transform: translateY(-50%);
    right: 0;
    z-index: 3;
}

.header-menu li {
    display: flex;
    flex-direction: row;
    display: inline-flex;
    padding-left: 20px;
    padding-right: 20px;
    font-family: Source Sans Pro;
    font-weight: 400;
    font-size: 1.25rem;
}

@media only screen and (max-width: 950px) {
    .header :nth-child(1) {
        order: 1;
    }
    .header :nth-child(2) {
        order: 2;
        align-content: flex-end;
    }

    .typography {
        margin-top: auto;
        margin-bottom: auto;
    }
    .header-image {
        width: 20vw;
        height: auto;
        display: block;
        justify-content: center;
        align-items: center;
        object-fit: contain;
    }
}

.modal-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    outline: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 2;
}

.modal-background {
    position: fixed;
    top: 95px;
    right: 0;
    z-index: 00;
    background: #772432;
    float: right;
    z-index: 100;
}

.modal-body {
    position: relative;
    right: 0;
    bottom: 0;
    top: 0;
    left: 0;
    width: 50vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

.modal-menu-content {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    max-height: 30rem;
    overflow-x: hidden;
    overflow-y: auto;
    font-size: 2.5rem;
    text-decoration: none;
}

.modal-menu-content li {
    list-style: none;
    text-decoration: none;
    margin-top: 40px;
    margin-bottom: 40px;
}


.navbar-logo {
    display: inline-flex;
}

.logo-image {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 90px;
}
.ptm-logo-text {
    width: auto;
    height: 100px;
    display: block;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.typography {
    font-family: 'Montserrat', sans-serif;    
    font-weight: 600;
    font-size: 1.75rem;
    margin: 1%;
    margin-top: auto;
    margin-bottom: auto;

}

@media only screen and (max-width: 1000px) {
    .hidden {
        display: none;
    }
}
.footer {
    position: absolute;
    width: 100vw;
    height: auto;
    background-color: #004165;
    box-shadow: 0px 1px 5px #888, 0px -10px 5px #a9b2b1;
    margin-top: 2%;
    color: #ffffff;
    display: flex;
    flex-direction: row;
    padding-top: 2%;
    padding-bottom: 2%;
    padding-right: auto;
    padding-left: auto;
    font-family: Source Sans Pro;
}

.footerImage {
    width: auto;
    height: 150px;
    margin-left: auto;
    margin-right: auto;
    display: block;
}


.footer ul, li {
    margin-left: 0px;
    padding: 0px;
    list-style-type: none;
}

.tagline{
    width: 100%;
}


.footerSection  {
    display: flex;
    flex-direction: column;
    max-width: 40vw;
    flex-shrink: 5;
    flex-grow: 1;
}

.usefulLinks{
    align-items: flex-end;
    text-align: right;
    flex-direction: column;
    list-style-type: none;
    padding: 0px;
    margin: 15px 0;
}

.centreInDiv {
    margin-top: 10px;
    display: block;
    text-align: center;
}

.centre{
    align-items: center;
}

.button {
    color: rgb(19, 19, 19);
}



@media only screen and (max-width: 1000px) {
    .footer {
        position: absolute;
        width: 100vw;
        height: auto;
        background-color: #004165;
        box-shadow: 0px 1px 5px #888, 0px -10px 5px #a9b2b1;
        margin-top: 2%;
        color: #ffffff;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;

    }

    .usefulLinks {
        text-align: center;
        list-style-type: none;
      }


    .mobile-footerSection {
        /* On small screens, we are no longer using row direction but column */
        margin-left: 30px;
        margin-right: 30px;
        text-align: left;
        min-width: 200px;
        display: inline-block;
        justify-content: space-evenly;
        /* how much space taken flexing */
    }

    .footerImage {
        max-width: 200px;
        height: auto;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        display: block;
    }

    .tagline{
        max-width: 600px;
    }
    .footerSection {
        display: inline-block;
        justify-content: space-around;
        vertical-align: top;
        max-width: 100vw;
        align-items: center;
    }

    .footerPTMDetails {
        margin-left: auto;
        margin-right: auto;
    }

    .footerPTMDetails h4,
    h5,
    p {
        margin: 5px;
    }

    .footerPTMDetails p {
        font-size: 14px;
    }

    .footerPTMDetails h3 {
        font-size: 16px;
    }

    .vertical-center {
        margin: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }

    .join-button a {
        font-size: 14px;
    }
    .query-button a {
        font-size: 14px;
    }
}

.join-button {
    background-color: #288316;
    color: rgb(255, 255, 255);
    border-radius: 12px;
    height: 50px;
    width: 170px;
    border: transparent;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    font-weight: 600;
}
a {
    text-decoration: none;
    color: #ffffff;
}

a:hover {
    text-decoration: underline;
    color: #ffffff;
}

a:checked {
    text-decoration: none;
    color: #ffffff;
}

.query-button {
    background-color: rgb(173, 55, 0);
    color: rgb(255, 255, 255);
    border-radius: 12px;
    height: 50px;
    width: 170px;
    border: transparent;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    font-weight: 600;
}

.about-body {
    margin-left: 10%;
             margin-right: 10%;
             display: flex;
}
.text {
    font-size: 1.25rem;
    text-align: left;
    font-family: Source Sans Pro;
    font-weight: 400;
    margin-right: 5vw;
    display: inline-block;
}
.text h2 {
    text-align: center;
}
.text h3 {
    text-align: center;
}
.text button {
    margin-left: auto;
    margin-right: auto;
}

.about-body img {
    max-width: 600px;
}

@media only screen and (max-width: 1400px) {
  .about-body  img {
        display: none;
    }
}
.jumbo {
    display: flex;
    max-height: 80vh;
    background-repeat: no-repeat;
    background-size: 100% 85%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    position: relative;
    align-items: self-end;
    justify-content: flex-end;
    top: 0;
    left: 0;
}
.background-video {
    overflow: hidden;
    object-fit: fill;
    max-height: 80vh;
    display: flex;

    top: 95px;
    left: 0;
    width: 100%;
    z-index: 2;
}

video {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    object-fit: fill;
    max-height: 80vh;
    display: flex;
    z-index: 2;
    vertical-align: middle;
}

.overlay-desc {
    background: rgba(0, 0, 0, 0);
    position: absolute;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.jumbo-text {
    text-align: center;
    background-color: white;
    opacity: 0.9;
    display: block;
    position: relative;
    padding: 1vh;
    font-size: 1.25rem;
    z-index: 2;
}

.jumbo-text h2 {
    font-family: Source Sans Pro, sans-serif;
    font-weight: 600;
    text-align: center;
    font-size: 36px;
    margin: 10px;
}

.jumbo-text h3 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    text-align: center;
    margin: 0;
}

.jumbo-text p {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 16px;
}

@media only screen and (max-width: 950px) {
    .jumbo-text {
        text-align: center;
        background-color: white;
        position: relative;
        padding: 2vh;
        margin-left: auto;
        margin-right: auto;
    }
    .overlay-desc {
        background: rgba(0, 0, 0, 0);
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .jumbo-text h2 {
        text-align: center;
    }

    .jumbo-text h3 {
        text-align: center;
    }
}

.top-triangle {
    -webkit-clip-path: polygon(100% 0%, 0% 100%, 100% 100%);
            clip-path: polygon(100% 0%, 0% 100%, 100% 100%);
    z-index: -1;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0px 161px 100vw;
    border-color: transparent transparent #004165;
    position: static;
    position: initial;
    -webkit-clip-path: x= '0' y= '110' width= '90' height= '90';
            clip-path: x= '0' y= '110' width= '90' height= '90';
}

.rectangle {
    background-color: #004165;
    width: 100%;
    height: auto;

    position: static;

    position: initial;
    text-align: center;
}

.down-triangle {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 161px 100vw 0 0px;
    border-color: #004165 transparent transparent;
    position: static;
    position: initial;
    /* filter: drop-shadow(0px 5px 5px #424242); */
}

.blockquote {
    color: #ffffff;
    width: auto;
    display: table;
    text-align: center;
    margin: 0 auto;
    font-family: Source Sans Pro;
}

.testimony-text {
    font-family: Source Sans Pro;
    font-weight: 500;
    font-size: 1.25rem;

    
}

.member-testimony {
    display: flex;
    flex-direction: row;
    margin-left: 7%;
    margin-right: 7%;
    
}

@media only screen and (max-width: 1400px) {
    .member-testimony {
        display: flex;
        flex-direction: row;
        margin-left: 7%;
        margin-right: 7%;
        flex-wrap: wrap;
    }
}

.testimony {
    margin: 3%;
    flex-basis: 100%;
    text-align: justify; 
}

.testimony img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    flex-basis: 100%;
    height: 200px;
    width: 160px;
    align-items: center;
    border-radius: 50%;
}

.faq-body{
    min-height: 80vh;
